html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
}

.window__youtube {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: fixed;
    height: 137px;
    padding: 25px;
    border-radius: 2px;
    box-sizing: border-box;
    z-index: 3;
    bottom: 0;
    background-image: url('../images/window__bg__image.jpg');
    background-repeat: no-repeat;
    background-size: contain;
}

.img__youtube__window {
    width: 36px;
    margin-top: 20px;
}

.presentation__text {
    text-align: center;
    padding: 5px;
    animation: blink 2s infinite;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    font-weight: bold;
    font-family: sans-serif;
    color: black;
}
@keyframes blink {
    from { opacity: 1; }
    to { opacity: 0; }
}


#slides {
    position: relative;
    height: 450px;
    padding: 0;
    margin: 0 0 15px;
    list-style-type: none;
}

.slide {
    display: flex;
    justify-content: center;
    gap: 20px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 1;

    -webkit-transition: opacity 1s;
    -moz-transition: opacity 1s;
    -o-transition: opacity 1s;
    transition: opacity 1s;
}

.showing {
    opacity: 1;
    z-index: 2;
}

.slide {
    font-size: 40px;
    padding: 40px;
    box-sizing: border-box;
    background: #333;
    color: #fff;
}

.slide:nth-of-type(1) {
    background: indianred;
}

.slide:nth-of-type(2) {
    background: darkseagreen;
}

.slide:nth-of-type(3) {
    background: cadetblue;
}

.slide:nth-of-type(4) {
    background: darkseagreen;
}

.container__img {
    border: 1px solid red;
    margin: 5px;
    display: flex;
    justify-content: center;

}

.img__lookbook {
    border-radius: 8px;
}

.logo__container {
    display: flex;
    justify-content: center;
    padding:  10px;
}

.logo__img {
    width: 90px;
}

.container__goods {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
    margin-bottom: 40px;
}

.good {
    transition: 1s;
}

.good:hover {
    transform: scale(1.09);
}

.shirt_good {
    width: 355px;
}

.mushrooms_shirt {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.cosmo_shirt {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.bags__logo {
    display: flex;
    margin: 15px auto;
}

.bag_good {
  width: 200px;
  margin-right: 160px;
}

.bag_good:last-child {
    margin-right: 0;
}

.youtube__cont {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    padding: 2px;
}

.img__youtube {
    width: 26px;
    border-radius: 10px;
    transition: box-shadow 1s;
}

.img__youtube:hover {
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.footer {
    border-top: 2px solid darkgrey;
}

@media (max-width: 1178px) {
    .tablet_img_slide {
        display: none;
    }

    .showing {
        gap: 50px;
    }
}


@media (max-width: 768px) {
    .mobile_img_slide {
        display: none;
    }

    .mushrooms_shirt {
        flex-direction: column;
    }

    .cosmo_shirt {
        flex-direction: column;
    }

    .good {
        margin: 0 auto;
    }
}

@media (max-width: 568px) {
    .container__goods {
        padding: 0;
    }

    .mobile_img_slide {
        display: none;
    }

    .window__youtube {
        display: none;
    }
}

@media (max-width: 372px) {
    .good {
        width: 100%;
    }
}
